const routes = [
  {
    path: "/",
    name: "App",
    component: App,
  },

  // {
  //   path: '/signup',
  //     name: "SignUp",
  //   component: SignUp,
  // },

  // {
  //   path: '/login',
  //     name: "Login",
  //   component: Login,
  // },
]

 
const router = createRouter({
  history: createWebHistory(),

  routes
})

export default router


